.dialogs {
  display: grid;
  grid-template-columns: 2fr 10fr;
}
.dialogsItems {
  padding: 10px;
  color: white;
}
.dialogsItems .active {
  color: gold;
}
.dialog {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 8px;
}
.messages {
  padding: 10px;
}
.messages .message {
  color: white;
}
.person {
  display: grid;
  grid-template-columns: 1fr 2fr;
  margin-bottom: 10px;
}
.avatar img {
  height: 50px;
  border-radius: 50%;
}
